import uni_request from './uni_request.js'
import appConst from '../enums/app-const.js'
import cache from './cache.js'
import config from '../config/config.js'
import router from './router.js'

const request = uni_request({ // 
    baseURL: config.serverUrl, //baseURL
    timeout: 60000 // 
})

request.interceptors.request.use(async (config, ...args) => {
	//token
	//token
	let clientToken = cache.get(appConst.CLIENT_TOKEN_NAME)
	if(clientToken && clientToken != null || clientToken != ''){
		config.header[appConst.SERVER_TOKEN_NAME] = clientToken
	}
    return config
})


request.interceptors.response.use((response, ...args) => { // , 
   /* const { data: res } = response // args[0] method args[1] url args[3] data
    await new Promise(resolve => setTimeout(() => resolve(), 3000))
    if (res.code === 200) {
        console.log('  3 s')
    } */
	//console.log(':'+JSON.stringify(response.data))
	//
	if(response.data.code === 400 || response.data.code === 10007){
		//
		cache.clear()
		router.reLaunch('/pages/login/login2')
	}
    return response
})

request.onerror = async (...args) => { // 
	console.log('', `url${args[1]}`)
}

let _request = {
	get(url,data){
		return new Promise(function(resolve, reject){
			request.get(url,data).then(res => {
				if(res.success){
					resolve(res.result)
				}else{
					let msg = res.message;
					if(msg === undefined || msg === null || msg === ''){
						msg = ''
					}
					reject(msg)
				}
			}).catch(err => {
				reject(err)
			})
		})
		
	},
	post(url,data){
		return new Promise(function(resolve, reject){
			request.post(url,data).then(res => {
				if(res.success){
					resolve(res.result)
				}else{
					let msg = res.message;
					if(msg === undefined || msg === null || msg === ''){
						msg = ''
					}
					reject(msg)
				}
			}).catch(err => {
				reject(err)
			})
		})
		
	}
}

export default _request